import React from 'react';
import Details from './components/Details';
import './App.css';
import datas from './datas';

function App() {
  return (
    <div className="App">

      <div className="container">
        {datas.map(data => {
          return (<Details 
            img={data.img}
            name={data.name}
            dob={data.dob}
          />)
        })}
      </div>

    </div>
  );
}

export default App;
